<template>
    <div class="body">
        <div class="navbody">
            <img src="@/assets/HOME/first-fruit-1.png" alt="Product 1" class="navimg">
        </div>
        <div class="sectionbody">
            <img :src="currentImageUrl" alt="Carousel Image" class="carousel-img" />
        </div>
    </div>
</template>

<script>
// import axios from "axios";

export default {
  data() {
    return {
      images: [], // 存儲從 API 獲取的圖片 URL
      currentIndex: 0,
      carouselInterval: null,
    };
  },
  computed: {
    currentImageUrl() {
      // 返回當前 API 加載的圖片
      return this.images.length ? this.images[this.currentIndex] : null;
    },
  },
  methods: {



    async fetchData() {
      try {
        const response = await axios.get("http://34.168.66.212:5000/v1/pro");
        const productData = response.data.data;

        // 完整的圖片 URL
        this.images = productData.map(
          (product) => `http://34.168.66.212/${product["圖片位置"]}`
        );


        product
        if (!this.images.length) {
          console.error("圖片資料為空或格式不正確");
        }
      } catch (error) {
        console.error("抓取圖片資料時發生錯誤:", error);
      }

    },
    startCarousel() {
      this.carouselInterval = setInterval(() => {
        // 每5秒切換到下一張圖片
        this.currentIndex = (this.currentIndex + 1) % this.images.length;
      }, 5000);
    },
    stopCarousel() {
      if (this.carouselInterval) {
        clearInterval(this.carouselInterval);
      }
    },
  },
  mounted() {
    this.fetchData(); // 初始化抓取圖片
    this.startCarousel(); // 啟動輪播
  },
  beforeUnmount() {
    this.stopCarousel(); // 組件卸載時停止輪播
  },
};
</script>

<style>
.body {
    display: flex;
    justify-content: space-between;
}

.navbody {
    width: 450px;
    height: 100vh;
}

.navimg {
    width: 100%;
    height: 80%;
}

.sectionbody {
    background-image: url('@/assets/HOME/tv.png');
    background-size: 50%;
    background-position: center;
    background-repeat: no-repeat;
    width: 100%;
    height: 100vh;
    /* border: #000 1px solid; */
}
</style>