<template>
  <header class="hed">
    <div class="logo-div">
      <router-link to="/"><img src="@/assets/LOGOS/logo.png" alt="logo" class="logo-img"></router-link>
    </div>

      <div class="mem">
       <router-link to="member" ><img src="@/assets/LOGOS/member.png" alt="member" class="member-img"></router-link> 
        <router-link to="car"><img src="@/assets/LOGOS/shopcar.png" alt="shopcar" class="shopcar-img"></router-link>
        <div class="dropdown">
          <button @click="toggleMenu" class="hamburger">
            <div class="bar"></div>
            <div class="bar"></div>
            <div class="bar"></div>
          </button>
      <!-- 使用 Vue transition 來添加過渡效果 -->
          <transition name="fade-slide">
            <div v-if="isMenuOpen" class="dropdown-menu">
              <div class="rou">
                <router-link to="/" class="tex">首頁</router-link>
              </div>
              <div class="rou">
                <router-link to="show" class="tex">商品總覽</router-link>
              </div>
              <div class="nin">
                <button  @click="toggleSept" class="btn-nin">九月品項 ▼</button>
              </div>
            </div>
          </transition>

          <transition name="fade-slide">
            <div v-if="isSeptemberOpen" class="dropdown-menu2">
              <router-link to="page" class="tex">水蜜桃布丁燒</router-link>
              <router-link to="page" class="tex">肉桂捲</router-link>
              <router-link to="page" class="tex">紅酒葡萄紅茶</router-link>
            </div>
          </transition>
        </div>
      </div>
  </header>
</template>
  
<script>
  export default {
    data() {
      return {
        isMenuOpen: false,
        isSeptemberOpen:false,
      };
    },
    methods: {
      toggleMenu() {
        if (this.isSeptemberOpen) {
        this.isSeptemberOpen = false;
      }
        this.isMenuOpen = !this.isMenuOpen;
      },
      toggleSept() {
        this.isSeptemberOpen = !this.isSeptemberOpen;
      },
      closeMenu() {
        this.isMenuOpen = false;
        this.isSeptemberOpen = false;
      },
      handleClickOutside(event) {
        if (this.isMenuOpen && this.isSeptemberOpen && !this.$el.contains(event.target)) {
        this.closeMenu();
        }
      }
    },
    mounted() {
      document.addEventListener('click', this.handleClickOutside);
    },
    beforeDestroy() {
      document.removeEventListener('click', this.handleClickOutside);
    }
  };
  </script>
  
  <style scoped>
  /* 過渡動畫的樣式 */
  .fade-slide-enter-active, .fade-slide-leave-active {
    transition: opacity 0.3s ease, transform 0.3s ease;
  }
  .fade-slide-enter-from, .fade-slide-leave-to {
    opacity: 0;
    transform: translateY(-20px);
  }
  .fade-slide-leave-from, .fade-slide-enter-to {
    opacity: 1;
    transform: translateY(0);
  }
  
  .hed{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content:flex-end;
    align-items: flex-start;
    /* border: #000 1px solid; */
  }

  .logo-div{
    width: 70%;
    justify-items: center;
    /* border: 1px solid; */
  }

  .logo-img{
    width: 350px;
    height: 60px;
  }
  
  .mem{
    width: 10%;
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 10px;
    /* border: 1px solid; */
  }

  .member-img{
    width: 30px;
    height: 30px;
  }

  .shopcar-img{
    width: 30px;
    height: 30px;
  }

  .hamburger {
    background: none;
    border: none;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    gap: 5px;
  }
  
  .bar {
    width: 35px;
    height: 4px;
    background-color: black;
  }
  
  .dropdown-menu {
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: center;
    position: absolute;
    top: 35px;
    right: 0;
    background-color: white;
    /* border: 1px solid #2b2525; */
    box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    gap: 5px;
  }  

  .rou{
    margin-top: 5px;
    justify-items: center;
    /* border: #000 1px solid; */
  }
  .tex {
    background: none;
    color:black;
    text-decoration: none;
  }

  .btn-nin{
    background: none;
    border: none;
    margin-top: 5px;
  }

  .dropdown-menu2 {
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: center;
    position: absolute;
    top: 6rem;
    right: 7.3rem;
    background-color: white;
    /* border: 1px solid #2b2525; */
    box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    gap: 5px;
  }

  </style>
  